import React from 'react';
import s from './Map.module.css';


function Map() {
    function ListItem(props) {
        let text=props.value === "–ö–æ–º—É–Ω–Ω–∏—Å—Ç" ? s.red : "";
        return <p className={text+' '+s.qvol}>{props.value}</p>;
    }

    function QualitiesList(props) {
        const qualities = props.qualities;
        return (
            <div>
                {qualities.map((quality,i) =>
                    <ListItem key={i}
                              value={quality} />
                )}
            </div>
        );
    }

    const qualities = ["–†–∞—Ü–∏–æ–Ω–∞–ª–∏—Å—Ç","–ö–æ–º—É–Ω–Ω–∏—Å—Ç","–ê–Ω–∏–º–µ—à–Ω–∏–∫" ];

    return (

            <div id="mapDiv">
                <QualitiesList qualities={qualities} />

            </div>


    );
}

export default Map;
